<template>
  <!--  -->
  <div id="user_datum">
    <!--  -->
    <van-nav-bar
      title=""
      left-text=""
      id="title"
      left-arrow
      @click-left="$router.back()"
    />
    <!--  -->
    <input type="file" ref="img" accept="image/*" hidden @change="onChange" />
    <van-cell class="cell" title="" @click="$refs.img.click()" is-link>
      <van-image
        width="30"
        height="30"
        fit="cover"
        center
        round
        :src="info.photo"
      />
    </van-cell>
    <van-cell
      class="cell"
      title=""
      @click="show_name = true"
      is-link
      :value="info.name"
    />
    <van-cell
      @click="show_genter = true"
      class="cell"
      title=""
      is-link
      :value="info.gender == 0 ? '' : ''"
    />
    <van-cell
      @click="show_bir = true"
      class="cell"
      title=""
      is-link
      :value="info.birthday"
    />
    <!--  -->
    <!-- name -->
    <van-popup
      v-model="show_name"
      position="bottom"
      :style="{ height: '100%' }"
    >
      <UserName :uname="info.name" v-if="show_name" />
    </van-popup>
    <!-- -->
    <van-popup v-model="show_genter" position="bottom">
      <UserGenter :genter="info.gender" v-if="show_genter" />
    </van-popup>
    <!--  -->
    <van-popup v-model="show_bir" position="bottom">
      <UserBirthday v-if="show_bir" :time="info.birthday" />
    </van-popup>
    <!--  -->
    <van-popup
      v-model="show_img"
      duration="0"
      position="bottom"
      :style="{ height: '100%' }"
    >
      <UserImg :pre_imgobj="user_photo" v-if="show_img" />
    </van-popup>
  </div>
</template>

<script>
// 
import { getUser } from "@/apis/users";
import { Toast } from "vant";
// 
import userName from "./components/user_name.vue";
import userGenter from "./components/user_genter.vue";
import userBirth from "./components/user_birthday.vue";
import UserImg from "./components/user_photo.vue";
export default {
  name: "userDatum",
  components: {
    UserName: userName,
    UserGenter: userGenter,
    UserBirthday: userBirth,
    UserImg,
  },

  data() {
    return {
      // 
      show_name: false,
      show_genter: false,
      show_bir: false,
      show_img: false,
      //   
      info: {},
      // 
      user_photo: "",
    };
  },

  created() {
    this.getUserInfo();
  },
  mounted() {
    //   
    this.$bus.$on("back", (newName) => {
      // ------
      //   
      if (newName != null) {
        this.info.name = newName;
      }
      this.show_name = false;
    });
    // 
    this.$bus.$on("back_gender", (newGen) => {
      if (newGen != null) {
        this.info.gender = newGen;
      }
      this.show_genter = false;
    });
    // 
    this.$bus.$on("settime", (newtime) => {
      if (newtime != null) {
        this.info.birthday = newtime;
      }
      this.show_bir = false;
    });
    // 
    this.$bus.$on("updata_photo", (newimg) => {
      if (newimg != null) {
        this.info.photo = newimg;
      }
      this.show_img = false;
    });
  },
  beforeDestroy() {
    //   
    this.$bus.$off("back");
    this.$bus.$off("back_gender");
    this.$bus.$off("settime");
    this.$bus.$off("updata_photo");
  },

  methods: {
    //   
    async getUserInfo() {
      try {
        const res = await getUser();
        this.info = res.data.data;
      } catch (error) {
        Toast.fail("");
      }
    },

    // 
    onChange() {
      // 
      const blob = this.$refs.img.files[0];
      // 
      this.show_img = true;
      // 
      this.user_photo = blob;
      // input
      this.$refs.img.value = "";
    },
  },
};
</script>
<style lang="less" scoped>
#user_datum {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  width: 100%;
  #title {
    background-color: rgb(26, 16, 68);
    width: 100%;
    /deep/.van-nav-bar__text,
    /deep/.van-icon,
    /deep/.van-nav-bar__title {
      color: #fff;
    }
  }

  .cell {
    margin-top: 10px;
    width: 96%;
    border-radius: 10px;
  }
}
</style>