<template>
  <div id="user_name">
    <!--  -->
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      id="title"
      left-arrow
      @click-left="back()"
      @click-right="back(true)"
    />

    <!--  -->
    <van-cell-group inset>
      <van-field
        v-model="username"
        autosize
        type="textarea"
        maxlength="7"
        placeholder=""
        show-word-limit
      />
    </van-cell-group>
  </div>
</template>

<script>
import { Toast } from "vant";
import { setUser } from "@/apis/users";
export default {
  name: "userName",
  props: {
    uname: {
      Type: String,
      required: true,
    },
  },

  data() {
    return {
      username: this.uname,
    };
  },
  methods: {
    //   or
    back(is) {
      // 
      if (is) {
        //   name
        let nc = this.username.trim();
        // 
        if (nc.length < 1) {
          Toast.fail("");
          return;
        }
        // name
        if (nc == this.uname) {
          Toast.fail("");
          return;
        }
        this.setUserInfo(nc);
      }
      //   
      this.$bus.$emit("back");
    },

    // 
    async setUserInfo(uname) {
      Toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
      });
      try {
        const res = await setUser({
          name: uname,
        });
        Toast.success("");
        this.$bus.$emit("back", res.data.data.name);
      } catch (error) {
        //   
        Toast.fail(" ");
      }
    },
  },
};
</script>

<style lang="less" scoped>
.van-popup {
  background-color: #f5f7f9;
}
#user_name {
  width: 100%;
  // 
  #title {
    background-color: rgb(26, 16, 68);
    width: 100%;
    /deep/.van-nav-bar__text,
    /deep/.van-icon,
    /deep/.van-nav-bar__title {
      color: #fff;
    }
  }

  //   
  /deep/.van-cell-group {
    margin-top: 40px;
    border: 1px solid #ccc;
  }
}
</style>