<template>
  <div id="userimg">
    <img :src="preImg" ref="photo" />
    <!--  -->
    <van-nav-bar
      left-text=""
      right-text=""
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
  </div>
</template>

<script>
import { setPhoto } from "@/apis/users";
import { Toast } from "vant";
// 
import Cropper from "cropperjs";
import "cropperjs/dist/cropper.common";
export default {
  name: "UserImg",
  props: {
    //   
    pre_imgobj: {
      Type: Object,
      required: true,
    },
  },

  data() {
    return {
      // 
      preImg: window.URL.createObjectURL(this.pre_imgobj),
    };
  },

  mounted() {
    // dom;
    // const image = this.$refs.photo;
    // new Cropper(image, {
    //   viewMode: 1,
    //   dragMode: "move",
    //   aspectRatio: 1,
    //   cropBoxMovable: false,
    //   cropBoxResizable: false,
    //   background: false,
    //   movable: true,
    // });
  },

  methods: {
    //   
    onClickLeft() {
      this.$bus.$emit("updata_photo");
    },
    // 
    async onClickRight() {
      Toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
      });
      //   formData
      const d = new FormData();
      //   
      d.append("photo", this.pre_imgobj);

      try {
        //   
        await setPhoto(d);

        Toast.success("");
        // 
        this.$bus.$emit("updata_photo", this.preImg);
      } catch (error) {
        Toast.fail("");
      }
    },
  },
};
</script>

