<template>
  <!--  -->
  <div id="user_birthday">
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title=""
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="save()"
      @confirm="save(true)"
    />
  </div>
</template>

<script>
import { setUser } from "@/apis/users";
import { Toast } from "vant";
import dayjs from "dayjs";
export default {
  name: "userBirth",
  //   
  props: {
    time: { Type: [String, Number], required: true },
  },

  data() {
    return {
      // 
      minDate: new Date(1900, 0, 1),
      //   
      maxDate: new Date(),
      //   
      currentDate: new Date(this.time) ? new Date(this.time) : new Date(),
    };
  },

  methods: {
    //   or
    async save(is) {
      // 
      let time = dayjs(this.currentDate).format("YYYY-MM-DD");
      if (is) {
        // 
        Toast.loading({
          message: "...",
          forbidClick: true,
          duration: 0,
        });
        await setUser({ birthday: time });
        Toast.success("");
        this.$bus.$emit("settime", time);
        return;
      }
      //   
      this.$bus.$emit("settime");
    },
  },
};
</script>

